	module.exports = {"data":{"markdownRemark":{"html":"<p>Simple Artificial Neural Network, limited to 3 layers.\nBased on <a href=\"https://github.com/makeyourownneuralnetwork/\">Make Your Own Neural Network</a> by Tariq Rashid and <a href=\"https://github.com/shiffman/Neural-Network-p5\">p5 Neural Network</a> by Daniel Shiffman.</p>\n<h3>Example</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token comment\">// Create the Neural Network</span>\n<span class=\"token keyword\">var</span> knn <span class=\"token operator\">=</span> <span class=\"token keyword\">new</span> <span class=\"token class-name\">p5ml<span class=\"token punctuation\">.</span>NeuralNetwork</span><span class=\"token punctuation\">(</span>callback<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token comment\">// Add two image and Index associate with that image</span>\nknn<span class=\"token punctuation\">.</span><span class=\"token function\">addImage</span><span class=\"token punctuation\">(</span>imageOne<span class=\"token punctuation\">,</span> <span class=\"token string\">'1'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\nknn<span class=\"token punctuation\">.</span><span class=\"token function\">addImage</span><span class=\"token punctuation\">(</span>imageTwo<span class=\"token punctuation\">,</span> <span class=\"token string\">'2'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token comment\">// Make a prediction. Will return either 1 or 2</span>\nknn<span class=\"token punctuation\">.</span><span class=\"token function\">predict</span><span class=\"token punctuation\">(</span>video<span class=\"token punctuation\">,</span> callback<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<h2>Constructor</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token function\">NeuralNetwork</span><span class=\"token punctuation\">(</span>inputNodes<span class=\"token punctuation\">,</span> hiddenNodes<span class=\"token punctuation\">,</span> outputNodes<span class=\"token punctuation\">,</span> <span class=\"token operator\">?</span>learningRate<span class=\"token punctuation\">)</span>\n</code></pre>\n      </div>\n<blockquote>\n<p><code>inputNodes</code> - Amount of input nodes.</p>\n</blockquote>\n<blockquote>\n<p><code>hiddenNodes</code> - Amount of hidden nodes.</p>\n</blockquote>\n<blockquote>\n<p><code>outputNodes</code> - Amount of output nodes.</p>\n</blockquote>\n<blockquote>\n<p><code>learningRate</code> - Learning Rate. Optional. Defaults to 0.1.</p>\n</blockquote>\n<h2>Properties</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token punctuation\">.</span>iNodes\n</code></pre>\n      </div>\n<blockquote>\n<p>Amount of input nodes.</p>\n</blockquote>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token punctuation\">.</span>hNodes\n</code></pre>\n      </div>\n<blockquote>\n<p>Amount of hidden nodes.</p>\n</blockquote>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token punctuation\">.</span>oNodes\n</code></pre>\n      </div>\n<blockquote>\n<p>Amount of output nodes.</p>\n</blockquote>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token punctuation\">.</span>wih\n</code></pre>\n      </div>\n<blockquote>\n<p>Input to Hidden weights.</p>\n</blockquote>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token punctuation\">.</span>who\n</code></pre>\n      </div>\n<blockquote>\n<p>Hidden to Output weights.</p>\n</blockquote>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token punctuation\">.</span>learningRate\n</code></pre>\n      </div>\n<blockquote>\n<p>Learning Rate</p>\n</blockquote>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token punctuation\">.</span>math\n</code></pre>\n      </div>\n<blockquote>\n<p>The environment Math element.</p>\n</blockquote>\n<h2>Methods</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token punctuation\">.</span><span class=\"token function\">train</span><span class=\"token punctuation\">(</span>input<span class=\"token punctuation\">,</span> target<span class=\"token punctuation\">)</span>\n</code></pre>\n      </div>\n<blockquote>\n<p>Trains the Neural Network.</p>\n</blockquote>\n<blockquote>\n<p><code>input</code> - The input array.</p>\n</blockquote>\n<blockquote>\n<p><code>target</code> - The target value of the input array.</p>\n</blockquote>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token punctuation\">.</span><span class=\"token function\">query</span><span class=\"token punctuation\">(</span>input<span class=\"token punctuation\">)</span>\n</code></pre>\n      </div>\n<blockquote>\n<p>Query the Neural Network based on the input and predicts an output. Returns ...</p>\n</blockquote>\n<blockquote>\n<p><code>input</code> -  The input array.</p>\n</blockquote>\n<h2>Source</h2>\n<p><a href=\"https://github.com/cvalenzuela/p5-deeplearn-js/blob/master/src/NeuralNetwork/index.js\">/src/NeuralNetwork/index.js</a></p>","frontmatter":{"path":"/docs/NeuralNetwork","name":"Neural Network"}}},"pathContext":{}}

